	.slick-btn {
		position: absolute;
		top: 96%;
		width: 20px;
		height: 14px;
		cursor: pointer;
		outline: none;
		z-index: 5;
		background-color: transparent;
		border: none;
		cursor: pointer; }
	.slick-btn.slick-disabled {
		cursor: default;
		background-color: transparent;
		opacity: 0.5; }
	.slick-next::before {
		position: absolute;
		background: url(../img/icon/arrow-left.svg);
		background-size: cover;
		content: "";
		transform: rotate(180deg);
		width: 20px;
		height: 14px;
		top: 0;
		right: 0;
		transition: 0.4s ease-out; }
	.slick-next:hover {
		transition: 0.4s ease-out;
		right: 60px; }
	.slick-next:hover::before {
		width: 40px;
		transition: 0.4s ease-out; }

	.slick-prev {
		right: 116px; }
	.slick-next {
		right: 76px;
		transition: 0.4s ease-out; }
	.slick-prev::before {
		position: absolute;
		background: url(../img/icon/arrow-left.svg);
		background-size: cover;
		content: "";
		width: 20px;
		height: 14px;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		transition: 0.4s ease-out; }
	// .slick-prev::before::hover
	// 	transform: scale(1.2)
	.slick-prev:hover::before {
		width: 40px;
		transition: 0.4s ease-out; }


