// Font face
@mixin font_face($font-family, $file-path, $weight: normal, $style: normal, $asset-pipeline: false ) {
	@font-face {
		font-family: $font-family;
		font-weight: $weight;
		font-style: $style;

		@if $asset-pipeline == true {
			src: font-url('#{$file-path}.ttf');
			src: font-url('#{$file-path}.ttf') format('truetype'); }
		@else {
			src: url('#{$file-path}.ttf');
			src: url('#{$file-path}.ttf') format('truetype'); } } }



// Horizontal gradient - from left to right
@mixin gradient_x($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {
  background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);
  background-repeat: repeat-x; }

// Vertical gradient - from top to bottom
@mixin gradient_y($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {
  background-image: linear-gradient(to bottom, $start-color $start-percent, $end-color $end-percent);
  background-repeat: repeat-x; }



// Media queries
// max-width
@mixin xxxl {
  @media only screen and (max-width: $xxxl) {
    @content; } }

@mixin xxl {
  @media only screen and (max-width: $xxl) {
    @content; } }

@mixin xl {
